.header__section {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

.header__logo {
  align-items: center;
  width: 199px;
  height: 50px;
}

.header {
  @include font-design(DM Sans, normal, 700, 16px, 1.28, 0.04em);
  background-color: $cl-section-hero;
  color: #fff;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-top: 6px;
  }
  @include tab {
    padding-top: 37px;
  }
}

// === меню ===
.nav {
  &__bar {
    display: flex;
    justify-content: flex-start;
    margin-left: 110px;
  }

  &__item {
    margin-right: 35px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    color: $cl-white-primary;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
      color: $cl-accent;
      text-shadow: 0px 5px 5px rgba(212, 20, 67, 0.6);
    }
  }
}

.site-menu {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  color: $cl-white-primary;

  @media screen and (min-width: 320px) and (max-width: 1279px) {
    display: none;
  }
}

//  ===== мобільне меню =====

.menu__block {
  padding: 0;
}
.mob {
  &-menu {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 60px 0 0;
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    height: 100vh;
    z-index: 9;
    background-color: $cl-menu-bg;
    box-shadow: -8px 0px 20px rgba(0, 0, 0, 0.25);
    transform: translate(0);
    transition: transform 400ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
      transform: translate(100%);
    }

    @include tab {
      justify-content: space-between;
      width: 254px;
      height: 100vh;
    }
  }

  &-menu__open {
    display: block;
    position: absolute;
    margin-left: auto;
    cursor: pointer;
    background: transparent;
    border: none;

    @media screen and (min-width: 320px) and (max-width: 767px) {
      top: 19px;
      right: 20px;
    }
    @include tab {
      top: 52px;
      right: 224px;
    }

    @media screen and (min-width: 1279px) {
      display: none;
    }
  }

  &-menu__close {
    display: block;
    margin-left: auto;
    cursor: pointer;
    position: absolute;
    top: 17px;
    right: 21px;
    background: transparent;
    border: none;
  }
}
.icon-cross {
  fill: #fff;
}
.mob-nav {
  position: relative;
  &__item {
    @include font-design(DM Sans, normal, 700, 14px, 1.28, 0.04em);
    display: flex;
    box-sizing: border-box;
    align-items: center;
    height: 42px;
    border-bottom: 0.5px solid #e18298;

    &:first-child {
      border-top: 1px solid #e18298;
    }
    &:hover,
    &:focus {
      color: $cl-accent;
      box-shadow: 0px 0px 4px 0px rgb(0 0 0 / 25%);
      transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
  }

  &__link {
    display: flex;
    align-items: center;
    margin-left: 21px;
    width: 100%;
    position: relative;
    color: $cl-white-primary;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @include tab {
      display: flex;
      align-items: center;
      margin-left: 24px;
    }
  }
}

//  === кнопки ===
.btn__buy {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: $cl-white-primary;
  border-style: none;
  border-radius: 22px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    display: none;
  }

  @include tab {
    position: absolute;
    top: 39px;
    right: 26px;
    width: 160px;
    height: 44px;
  }

  @media screen and (min-width: 1279px) {
    top: 39px;
    right: 115px;
    width: 159px;
    height: 44px;
  }

  &:hover,
  &:focus {
    background-color: $cl-hover-bg2;
    text-shadow: 0px 5px 5px rgba(212, 20, 67, 0.6);
  }

  &-link {
    @include font-design(DM Sans, normal, 700, 16px, 1.31, 0.04em);
    color: $cl-accent;
  }
}

.btn__buy-mob {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 19px;
  margin-left: auto;
  margin-right: auto;
  border-style: none;
  border-radius: 22px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    width: 205px;
    height: 44px;
  }

  @include tab {
    width: 186px;
    height: 44px;
  }
}

// == поточна сторінка ==
.current {
  color: $cl-accent;
  text-shadow: 0px 5px 5px rgba(212, 20, 67, 0.6);
}
